body {
  color: var(--text-dark-color);
  background-color: var(--background-light-color);
  font-family: Montserrat, sans-serif;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-transparent-color: #1111111a;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

* {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.hero-section {
  background-color: var(--background-dark-color);
  border-radius: 30px;
  margin: 19px 20px 71px;
  padding: 395px 20px 20px;
}

.hero-container {
  flex-direction: column;
  gap: 32px;
  display: flex;
}

.hero-sub-headline {
  color: var(--text-light-color);
  letter-spacing: -.0375em;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.hero-modal-and-link-button {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
  display: flex;
}

.hero-modal-button-first-line {
  justify-content: space-between;
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-buy-now-button {
  color: var(--text-light-color);
  background-color: var(--background-orange-color);
  cursor: pointer;
  transition-property: background-color, color;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-tf);
  border: none;
  border-radius: 100px;
  margin: auto;
  padding: 12px 28px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: block;
}

.hero-buy-now-button:hover, .hero-buy-now-button:focus {
  background-color: var(--background-button-color);
  color: var(--text-orange-color);
}

.hero-how-it-is-made-button {
  color: var(--text-light-color);
  border: 1px solid var(--text-light-color);
  cursor: pointer;
  transition-property: background-color, border;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-tf);
  background-color: #0000;
  border-radius: 100px;
  margin: auto;
  padding: 12px 28px;
  display: block;
}

.hero-how-it-is-made-button:hover, .hero-how-it-is-made-button:focus {
  background-color: var(--background-white-color);
  border: 1px solid var(--text-orange-color);
}

.hero-link-made-button {
  color: var(--text-light-color);
  transition-property: color;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-tf);
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.hero-link-made-button:hover, .hero-link-made-button:focus {
  color: var(--text-orange-color);
}

.hero-scroll-button-box {
  color: var(--text-light-color);
  fill: var(--text-orange-color);
  transition: background-color var(--transition-duration) var(--transition-tf);
  background-color: #0000;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 12px;
  display: flex;
}

.hero-scroll-button-box:hover .hero-scroll-down-button, .hero-scroll-button-box:focus .hero-scroll-down-button {
  background-color: var(--background-orange-color);
  fill: var(--text-light-color);
}

.hero-link-scroll {
  color: var(--text-light-color);
  letter-spacing: -.0225em;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.17;
}

.hero-scroll-down-button {
  width: 28px;
  height: 28px;
  background-color: var(--background-white-color);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  padding: 0;
  display: block;
}

.hero-button-scroll-icon {
  transition: fill var(--transition-duration) var(--transition-tf);
}

